// @flow
import * as React from 'react';
import clsx from 'clsx';
import {
  AppBar,
  makeStyles,
  Container,
  Link,
  ListItem,
  ListItemIcon,
  ListItemText,
  Grid,
  Typography,
} from '@material-ui/core';
import { ShowChart, HomeWork, CalendarToday } from '@material-ui/icons';
import _get from 'lodash/get';

import { Auth } from 'aws-amplify';
import imageLogo from '../../images/logo_n.svg';

const useStyles = makeStyles(theme => ({
  root: {
    margin: 'auto',
    position: 'sticky',
    top: 0,
  },
  container: {
    display: 'flex',
    alignItems: 'center',
    minHeight: '60px',
  },
  header: {
    background: theme.palette.common.white,
    minHeight: '60px',
    boxShadow: 'none',
    border: '1px solid #E0E0E0',
  },
  menuItems: {
    display: 'flex',
  },
  menuItemLink: {
    display: 'flex',
    alignItems: 'center',
    padding: '12px 13px',
    '&:hover, &:focus': {
      textDecoration: 'none',
    },
  },
  menuItemText: {
    color: theme.palette.primary.gray,
    fontFamily: theme.typography.fontFamily.avenirLT,
    textTransform: 'uppercase',
    fontWeight: 900,
    fontSize: '0.625rem',
    lineHeight: '0.75rem',
    letterSpacing: '1.54px',
    margin: '0px',
  },
  menuItem: {
    position: 'relative',
    '&:hover, &:focus': {
      background: 'none',
    },
    '&:hover, &.active': {
      '&:after': {
        width: '100%',
      },
      '& $menuIcon': {
        color: theme.palette.primary.main,
      },
      '& $menuItemText': {
        color: theme.palette.text.dark,
      },
    },
    '&:after': {
      content: "''",
      display: 'block',
      width: 0,
      height: '3px',
      background: theme.palette.primary.black,
      transition: 'width .3s',
      position: 'absolute',
      bottom: '-3px',
      left: 0,
    },
  },
  menuGrid: {
    display: 'flex',
  },
  actionGrid: {
    justifyContent: 'flex-end',
  },
  menuIcon: {
    minWidth: '24px',
    color: theme.palette.primary.gray,
    '& svg': {
      width: '16px',
      height: '16px',
    },
  },
  menuEmail: {
    marginRight: '30px',
  },
  menuUserSignOutBtn: {
    ...theme.typography.h5,
    '&:hover, &:focus': {
      background: 'none',
    },
  },
}));

function ListItemLink(props) {
  const classes = useStyles();
  return (
    <ListItem
      className={classes.menuItemLink}
      disableGutters={true}
      button
      component="a"
      {...props}
    />
  );
}

const Header = (): React.Element<typeof Container> => {
  const classes = useStyles();
  const isLoggedIn = Auth.user !== null;
  const email = _get(Auth, ['user', 'attributes', 'email'], '');
  const links = [
    ['DashBoard', <ShowChart key="chart" />, '/dashboard'],
    ['My Properties', <HomeWork key="homework" />, '/properties'],
    ['Calendar', <CalendarToday key="calendar" />, '/calendar'],
  ];

  const renderLinks = () => {
    return links.map((item, key) => {
      const [name, Icon, link] = item;
      const currentPath = window.location.pathname;
      return (
        <div
          className={clsx(classes.menuItem, {
            active: currentPath === link,
          })}
          key={key}
        >
          <Link href={link} className={classes.menuItemLink}>
            <ListItemIcon className={classes.menuIcon}>{Icon}</ListItemIcon>
            <ListItemText
              primary={<span className={classes.menuItemText}>{name}</span>}
            />
          </Link>
        </div>
      );
    });
  };

  const onClickSignOutHandler = async () => {
    await Auth.signOut();
  };

  return (
    <div className={classes.root}>
      {isLoggedIn ? (
        <AppBar position="static" color="default" className={classes.header}>
          <Container className={classes.container} maxWidth="lg">
            <Grid container alignItems="center">
              <Grid item md={1} sm={1} xs={1} className={classes.menuGrid}>
                <div className={classes.logo}>
                  <img src={imageLogo} height="40" alt="LogoW" />
                </div>
              </Grid>
              <Grid item md={6} sm={6} xs={6} className={classes.menuGrid}>
                <div className={classes.menuItems}>{renderLinks()}</div>
              </Grid>
              <Grid
                item
                xs={5}
                className={clsx(classes.menuGrid, classes.actionGrid)}
              >
                <div className={clsx(classes.menuEmail, classes.menuItemLink)}>
                  <Typography variant="h5">{email}</Typography>
                </div>
                <div className={clsx(classes.menuItemLink, classes.menuItem)}>
                  <ListItemLink
                    href="/"
                    className={classes.menuUserSignOutBtn}
                    onClick={onClickSignOutHandler}
                  >
                    <Typography variant="h5">Sign Out</Typography>
                  </ListItemLink>
                </div>
              </Grid>
            </Grid>
          </Container>
        </AppBar>
      ) : (
        <header className="top_header desktop">
          <div className="menu">
            <a href="/" className="menu_btn">
              Home
            </a>
            <a href="/about" className="menu_btn">
              About
            </a>
            <a href="/how-it-works" className="menu_btn">
              How It Works
            </a>
            <a href="/search" className="menu_btn">
              Search
            </a>
            <a href="/soldhomes" className="menu_btn">
              Sold Homes
            </a>
            <a href="/contact" className="menu_btn">
              Contact Us
            </a>
            <a href="/media" className="menu_btn">
              Media
            </a>
          </div>
        </header>
      )}
    </div>
  );
};

export default React.memo(Header);
